* {
	box-sizing: border-box;
}

/* --color__dark-rgb: 32, 39, 47; */

:root {
	--color__light: #FDFDFE;
	--color__light-alpha: rgba(253, 253, 254, 0.1);
	--color__light-alpha-norm: rgba(253, 253, 254, 0.4);
	--color__dark: #20272F;
	--color__dark-alpha: rgba(32, 39, 47, 0.1);
	--color__base-red: rgb(160, 61, 41);
	--color__complementary: rgb(41, 140, 160);
	--ease-in-out-circle: cubic-bezier(0.785, 0.135, 0.150, 0.860);
}

::selection {
	text-shadow: none;
	color: var(--color__dark);
	background-color: var(--color__light);
}

html {
	-webkit-tap-highlight-color: transparent;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	touch-action: manipulation;
}

html,
body {
	min-height: 100%;
}

body {
	font-family: BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
	font-size: 100%;
	font-weight: 400;
	line-height: 1.5;
	min-width: 320px;
	background-color: var(--color__light);
	color: var(--color__dark);
	margin: 0;
}

.fonts-loaded body {
	font-family: 'Anonymous Pro', monospace;
}

.fonts-loaded h1,
.fonts-loaded h2,
.fonts-loaded h3,
.fonts-loaded h4 {
	font-family: 'Poppins', sans-serif;
}

h1 {
	margin: 0;
}

h1, h2, h3, h4 {
	font-weight: 400;
}

button {
	padding: 0;
	border: 0;
	background: none;
	cursor: pointer;
}

button:focus {
	outline: none;
}

svg {
	display: inline-block;
	transition: 0.25s transform var(--ease-in-out-circle);
}

path,
rect {
	transition: fill, stroke-dash-array, stroke-dash-offset 0.2s var(--ease-in-out-circle);
}

figure {
	margin: 0;
}

a {
	color: inherit;
	border-bottom: 1px solid;
	text-decoration: none;
	position: relative;
	transition: border-bottom-color 0.25s ease-out;
}

a::after {
	content: '';
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	background-color: rgba(32, 39, 47, 0.1);
	transition: opacity 0.2s ease-out, transform 0.3s var(--ease-in-out-circle);
	opacity: 0;
}

.no-touchevents a:hover::after {
	opacity: 1;
	transform: rotate(-2.3deg);
}

.no-touchevents a:hover {
	border-bottom-color: rgba(0, 0, 0, 0);
}

.v-enter-active,
.v-leave-active {
	transition: opacity 1s var(--ease-in-out-circle);
}

.v-enter,
.v-leave-active {
	opacity: 0;
}

.page-wrap {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	padding-top: 2vh;
	padding-bottom: 2vh;
}

@media screen and (min-width: 72em) {
	.page-wrap {
		padding-left: 5vw;
	}
}
